import {
  CREATE_ROLE_REQUESTED,
  CREATE_ROLE_SUCCEEDED,
  CREATE_ROLE_FAILED
} from './constants';

export const createRoleRequested = data => ({ type: CREATE_ROLE_REQUESTED, payload: data });
export const createRoleSucceeded = data => ({ type: CREATE_ROLE_SUCCEEDED, payload: data });
export const createRoleFailed = data => ({ type: CREATE_ROLE_FAILED, payload: data });



