
/*
  eslint
  no-multi-spaces: 0
 */
const _validateRole = role => {
  if (!role)           { return { role: 'Роль не може бути порожнім' }; }
  if (role.length < 3) { return { role: 'Роль не може меньшим ніж 3 символів' }; }
  return {};
};

export const validateRole = values => ({
  ..._validateRole(values.role)
});



