import { connect } from 'react-redux';
import { reduxForm, reset } from 'redux-form';

import { UsersRoles } from '../../components';

import { validateRole as validate } from '../../util/validator';
import { createRoleRequested } from '../../redux/roles/actions';

const mapStateToProps = state => ({
});

const mapDispatchToProps = {
  createRoleRequested,
  reset
};

export default reduxForm({
  form: 'createRole',
  validate
})(connect(mapStateToProps, mapDispatchToProps)(UsersRoles));



